import React, { useEffect, useState } from 'react'
import { Alert, Col, Container, Nav, Row } from 'react-bootstrap'
import Button from 'react-bootstrap/Button';
import { useNavigate, useParams } from 'react-router-dom';
import Tab from './Tab';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
import { addItem } from '../states/cart';

const Box = styled.div`
    padding: 20px 0;
    color: gray;
    `;
const YellowBtn = styled.button`
    color: white;
    font-size: 30px;
    width: 100%;
    padding: 100px;
    border: 1px solid #ccc;
    background-image: url("https://images.unsplash.com/photo-1555378322-a8e1515568e4?q=80&w=1170&auto=format&fit=crop&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D");
    background-size:cover;
    background-position: center;`

const Detail = (props) => {
    let [tab, setTab] = useState(0);
    let { id } = useParams();
    let { shoes } = props;
    let selproduct = shoes.find((x)=> x.id==id);

    let [alert,setAlert] = useState(true);

    const dispatch = useDispatch();
    const navigate = useNavigate();

    useEffect(()=>{
        let a =  setTimeout(()=>{setAlert(false)},2000);

        return ()=> {
            clearTimeout(a);
        }
    })

    return (
        <Container>
            {
                alert === true ? <Alert variant='danger'>
                    2초 이내 구매시 할인
                    </Alert> : null
            }

            <Box>
                <YellowBtn>
                    지금 구매하면 10% 할인
                </YellowBtn>
            </Box>
            
            <Row>
                <Col md={6}>
                    <img src={process.env.PUBLIC_URL + selproduct.imgUrl} width="100%" alt="shoe" />
                </Col>
                <Col me={6}>
                    <h4 className='pt-5'> {selproduct.title}</h4>
                    <p> {selproduct.content}</p>
                    <p> {selproduct.price}</p>
                    <Button variant="success" onClick={()=>{dispatch(addItem({id: selproduct.id,item: selproduct.title, price: selproduct.price,amount:1}));
                    navigate('/cart');
                }}>주문하기</Button>
                </Col>
            </Row>

            <Nav variant="tabs" defaultActiveKey="link-0">
                <Nav.Item>
                    <Nav.Link eventKey="link-0" onClick={() => { setTab(0) }}>버튼0</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={() => { setTab(1) }}>버튼1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-2" onClick={() => { setTab(2) }}>
                        버튼2
                    </Nav.Link>
                </Nav.Item>
            </Nav>

            <Tab tab={tab} shoes={shoes} />
        </Container>
    )
}

export default Detail