import logo from './logo.svg';
import './App.css';
import { createContext, useState } from 'react';
import { Container, Nav, Navbar, Row } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import data from './data';
import Product from './components/Product';
import { Link, Route, Routes, useNavigate } from 'react-router-dom';
import Home from './components/Home';
import Detail from './components/Detail';
import About from './components/About';
import data2 from './data2';
import { useSelector } from 'react-redux';
import Cart from './components/Cart';

export let Context1 = createContext()

function App() {
  const user = useSelector(state => state.user);

  let navigate = useNavigate();
  let [shoes, setShoes] = useState(data);
  let [no, setNo] = useState([0, 1, 2, 3, 4, 5, 6, 7, 8]);
  let [nike, setNike] = useState(data2);
  let [remain, setRemain] = useState([10, 11, 12]);


  return (
    <div className="App">
      <Navbar bg="dark" data-bs-theme="dark">
        <Container>
          <Navbar.Brand>Navbar</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => { navigate('/') }}>Home</Nav.Link>
            <Nav.Link onClick={() => { navigate('/detail/0') }}>Detail</Nav.Link>
            <Nav.Link onClick={() => { navigate('/cart') }}>Cart</Nav.Link>
            <Nav.Link onClick={() => { navigate('/about') }}>About</Nav.Link>
          </Nav>
        </Container>
      </Navbar>

      {/* <Link to="/">홈</Link>
      <Link to="/detail">상세페이지</Link> */}

      <Routes>
        <Route path='/' element={<Home shoes={shoes} setShoes={setShoes} no={no} setNo={setNo} nike={nike} setNike={setNike} />} />
        <Route path='/detail/:id' element={
          <Context1.Provider value={{remain}}>     
            <Detail shoes={shoes} />
          </Context1.Provider>
        } />
        <Route path='/cart' element={<Cart />} />
        <Route path='/about' element={<About />}>
          <Route path='member' element={<div>멤버입니다.</div>} />
          <Route path='location' element={<div>위치입니다.</div>} />
        </Route>
        <Route path='*' element={<div>페이지를 찾을 수 없습니다.</div>} />
      </Routes>

      <p className='footer'>COPYRIGHT(C) 2024 Nike, Inc. All Rights Reserved</p>
    </div>
  );
}

export default App;
