import React, { useState } from 'react'
import { Container, Row, Button } from 'react-bootstrap';
import Product from './Product';
import Title from './Title';
import Nike from './Nike';
import axios from 'axios';

const Home = (props) => {
    let [count, setCount] = useState(1);
    const { shoes, setShoes, no, setNo, nike, setNike } = props;


    return (
        <>
            <div className="slider"></div>

            <Container>
                <Title tindex='1' />
                <Button variant="outline-primary" onClick={() => {
                    let copy = [...shoes];
                    copy.sort((a, b) => (a.title > b.title) ? 1 : -1)
                    setShoes(copy);

                    let no_copy = [];
                    for (let i in copy) {
                        no_copy.push(copy[i].id)
                    }
                    setNo(no_copy);
                }}>이름순</Button>{' '}
                <Button variant="outline-secondary" onClick={() => {
                    let copy = [...shoes];
                    copy.sort((a, b) => (a.price > b.price) ? 1 : -1)
                    setShoes(copy);

                    let no_copy = [];
                    for (let i in copy) {
                        no_copy.push(copy[i].id)
                    }
                    setNo(no_copy);
                }
                }>낮은가격순</Button>{' '}
                <Button variant="outline-success" onClick={() => {
                    let copy = [...shoes];
                    copy.sort((a, b) => (a.price < b.price) ? 1 : -1)
                    setShoes(copy);

                    let no_copy = [];
                    for (let i in copy) {
                        no_copy.push(copy[i].id)
                    }
                    setNo(no_copy);
                }}>높은가격순</Button>
                <Row>
                    {
                        shoes.map((shoe, i) =>
                            <Product shoes={shoe} key={i} i={i} no={no} />
                        )
                    }
                </Row>

                <Title tindex='2' />
                <Button variant="outline-warning" onClick={()=> {
                    if(count === 1){
                        axios.get('https://jungwoonan.github.io/react_data/nike2.json')
                        .then((result)=>{
                            let copy=[...nike, ...result.data];
                            setNike(copy);
                            setCount(2);
                        })
                    }else if(count === 2){
                         axios.get('https://jungwoonan.github.io/react_data/nike3.json')
                        .then((result)=>{
                            let copy=[...nike, ...result.data];
                            setNike(copy);
                            setCount(3);
                        })                       
                    }else {
                        alert('더이상 상품이 없습니다.')
                    }
                }}>
                    +3개 상품 더보기
                </Button>
                <Row style={{ marginTop: '30px' }}>
                     {
                        nike.map((nike, i)=>
                            <Nike nike={nike} key={i} />
                        )
                     }
                </Row>
            </Container>
        </>
    )
}

export default Home