import React from 'react'

const Title = (props) => {
    const {tindex} = props;
    let title,content

    const css1 = {
        marginTop: '70px',
        textAlign: 'center'
    }

    if(tindex=== '1'){
        title ="MD's Pick";
        content ="시선을 사로잡는 스타일링, 제품들을 만나보세요."
    }else if(tindex==='2'){
        title= "여름을 위한 컬렉션"
        content= "가볍게, 시원하게 썸머 컬렉션으로 여름을 준비해 보세요."
    }



  return (
    <>
        <h3 style={css1}>{title}</h3>
        <p>{content}</p>
    </>
  )
}

export default Title