import React from 'react'
import { Col } from 'react-bootstrap'

const Nike = (props) => {
    const {imgUrl, title, price} = props.nike;

    return (
        <>
            <Col md={4}>
                <img src={process.env.PUBLIC_URL + imgUrl} width='80%' alt="item2" />
                <h4>{title}</h4>
                <p>{price}</p>
            </Col>
        </>
    )
}

export default Nike