import React, { useContext, useEffect, useState } from 'react'
import { Context1 } from '../App';

const Tab = (props) => {
    let {remain} = useContext(Context1);
    const { tab, shoes } = props;
    let [fade, setFade] = useState('');

    const arr = [<div>내용0 : {remain[0]}</div>, <div>내용1</div>, <div>내용2</div>];
    // if (tab === 0) {
    //     return <div>내용0</div>
    // } else if (tab === 1) {
    //     return <div>내용1</div>
    // } else if (tab === 2) {
    //     return <div>내용2</div>
    // }

    useEffect(() => {
        setTimeout(()=>{setFade('end');},50)
        return ()=> {
            setFade('')
        }
    }, [tab])

    return (
        <div className={"start "+ fade}>
            {arr[tab]}
        </div>

    )
}

export default Tab